import React, { useState } from "react";
import { checkAgeAC, homeWorkReducer, sortAC } from "./bll/homeWorkReducer";
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";

export type UserType = InitialPeopleType[];
export type InitialPeopleType = { _id: number; name: string; age: number };
const initialPeople: UserType = [
  { _id: 0, name: "", age: 3 },
  { _id: 1, name: "", age: 66 },
  { _id: 2, name: "", age: 16 },
  { _id: 3, name: "", age: 44 },
  { _id: 4, name: "", age: 40 },
  { _id: 5, name: "", age: 55 },
];

function HW8() {
  const [people, setPeople] = useState<UserType>(initialPeople); // need to fix any

  // need to fix any
  const finalPeople = people.map((p: InitialPeopleType) => (
    <div key={p._id}>
      {p.name}, {p.age}
    </div>
  ));

  const sortUp = () => setPeople(homeWorkReducer(initialPeople, sortAC("up")));
  const sortDown = () =>
    setPeople(homeWorkReducer(initialPeople, sortAC("down")));
  const check = () => setPeople(homeWorkReducer(initialPeople, checkAgeAC(18)));

  return (
    <div>
      <hr />
      homeworks 8{/*should work ( )*/}
      {finalPeople}
      <div>
        <SuperButton onClick={sortUp}>sort up</SuperButton>
        <SuperButton onClick={sortDown}>sort down</SuperButton>
        <SuperButton onClick={check}>check 18</SuperButton>
      </div>
      <hr />
      {/*  ,  */}
      {/*<AlternativePeople/>*/}
      <hr />
    </div>
  );
}

export default HW8;
